import React from 'react'
import Logo from './Logo'
import './Home.css'
import {Link} from 'react-router-dom'


const Home = () => {
    return(
      <div className = 'Home'>
      <div className = 'heading'>
        <Logo/>
        &nbsp;&nbsp;
       <button type = "button" align = "left" className = 'signin'> <Link to = '/signin'>SIGN IN</Link></button>
        </div>
  <br/>
        <div className = "scrolling">
          <marquee> <b>Welcome to KFC</b> </marquee>
        </div>
  <br/>
        <div className = 'mid' >
          <p align = "center"> 
           <span className = "spanHome"><Link to = '/delivery'>DELIVERY</Link></span> &nbsp;&nbsp;&nbsp;   
          <span className = "spanHome"><Link to = '/takeaway'>TAKE AWAY</Link></span> &nbsp;&nbsp;&nbsp; 
          <span className = "spanHome"><Link to = '/dinein'>DINE IN</Link> </span></p>
          <img className = 'extend' src = 'https://media-assets-02.thedrum.com/cache/images/thedrum-prod/s3-news-tmp-10557-kfc_1--2x1--940.jpg' />
        </div>
      
  <br/>
  <br/>
  
  <div className = "menu">
    <p align = "center"> <Link to = '/menu'>CLICK HERE FOR MENU</Link></p>
    <br/>
    
  </div>
  
      
      </div>
    )
  }
  
  
  export default Home