import React, { useState } from 'react'
import './Takeaway.css'
import Data from './Data'
import Logo from './Logo'
import {Link} from 'react-router-dom'

const Takeaway = () =>{
   const initial = 0;
    const [num,setnum] = useState(initial)
    const [num1,setnum1] = useState(initial)
    const [num2,setnum2] = useState(initial)
    
    const increase =()=>{
        setnum(num+1)
    }
    const decrease =()=>{
        setnum(num-1)
    }

    const increase1 =()=>{
        setnum1(num1+1)
    }
    const decrease1 =()=>{
        setnum1(num1-1)
    }

    const increase2 =()=>{
        setnum2(num2+1)
    }
    const decrease2 =()=>{
        setnum2(num2-1)
    }

    const submit = () =>{
        alert("Order Confirmed Successfully")
        setnum(initial)
        setnum1(initial)
        setnum2(initial)
    }
    return(
        <div>
        <a><Link to = '/'>
        <Logo/>
        </Link> </a>
        <br/><br/>
        <div className = "takeaway" align = "center"  >
        <h1>Place your Order</h1> 

        <div className = 'image'>
        <img src = {Data[0].link}  alt = "new"/>
        <br/><br/>
        <h6>{Data[0].dname}</h6>
        <button onClick = {increase}>+</button>&nbsp;&nbsp;&nbsp;<span>{num}</span>&nbsp;&nbsp;&nbsp;<button onClick = {decrease}>-</button>
        </div>

        <div className = 'image'>
        <img src = {Data[1].link}  alt = "new"/>
        <br/><br/>
        <h6>{Data[1].dname}</h6>
        <button onClick = {increase1}>+</button>&nbsp;&nbsp;&nbsp;<span>{num1}</span>&nbsp;&nbsp;&nbsp;<button onClick = {decrease1}>-</button>
        </div>

        <div className = 'image'>
        <img src = {Data[2].link}  alt = "new"/>
        <br/><br/>
        <h6>{Data[2].dname}</h6>
        <button onClick = {increase2}>+</button>&nbsp;&nbsp;&nbsp;<span>{num2}</span>&nbsp;&nbsp;&nbsp;<button onClick = {decrease2}>-</button>
        </div>
        <br/><br/>
        <button className = "confirm"  type = "submit" onClick = {submit}>CONFIRM ORDER</button>
        <br/><br/>
        </div>
        </div>
    )
}

export default Takeaway;