import React from 'react'
import Logo from './Logo'
import './Dinein.css'


import {Link} from 'react-router-dom'


const Dinein = () =>{

    const submit = () =>{
        alert('Submitted Successfully')
    }

    return(
        <div>
         <a><Link to = '/'>
        <Logo/>
        </Link> </a>
        <br/><br/>
        <h1 align = 'center'>DINE IN</h1>
        <div className = "Dinein">
        <h3 align = 'center'>SELECT STATE</h3>
        <form align = 'center'>
            <select id = "city">
                <option></option>
                <option>Assam</option>
                <option>Andhra</option>
                <option>Bihar</option>
                <option>Haryana</option>
                <option>Kerala</option>
                <option>Madhya Pradesh</option>
                <option>Tamil Nadu</option>
                <option>West Bengal</option>
            </select>
            <h3 align = 'center'>SELECT OUR BRANCH</h3>
            <select id = "city">
                <option></option>
                <option>KFC - Kochi</option>
                <option>KFC - Coimbatore</option>
                <option>KFC - Banglore</option>
                <option>KFC - Calicut</option>
                <option>KFC - Mumbai</option>
                <option>KFC - Kolkata</option>
                <option>KFC - Ranchi</option>
                <option>KFC - Vishagapatnam</option>
            </select>
            <br/><br/>
           
            <button align = 'center' onClick = {submit}>CONFIRM</button>
        </form>
        
        </div>
        </div>
    )
}

export default Dinein;